"use client";

import { useQuery } from "@tanstack/react-query";
import { api } from "@/lib/api";
import WalletOpsGrid from "@/features/wallet-ops/components/wallet-ops-grid";

export default function WalletOpsPage() {
  const { data = [], isLoading, error, refetch } = useQuery<any[]>({
    queryKey: ["wallet-ops"],
    queryFn: () => api.getWalletOps(), // ← no args
    placeholderData: () => [],
    refetchInterval: 5000,
  });

  return (
    <div className="p-4 max-w-7xl mx-auto">
      <h1 className="text-xl font-semibold mb-3">Wallet Ops</h1>

      {isLoading && <div>Loading…</div>}

      {error && (
        <div className="text-destructive">
          Failed to load.{" "}
          <button className="underline" onClick={() => refetch()}>
            Retry
          </button>
        </div>
      )}

      <WalletOpsGrid rows={data} pageSize={20} />
    </div>
  );
}
