"use client";

import * as React from "react";
import type { ColumnDef } from "@tanstack/react-table";
import Link from "next/link";

import { DataGrid } from "@/components/tables/data-grid";
import {
  colHash,
  colNumber,
  fmtNum,
  hashShort,
} from "@/components/tables/column-helpers";

export type WalletRow = {
  wallet_id: string;
  token_count?: number | null;
  balance?: number | null;
  creator_pubkey?: string | null;
  status?: string | null;
};

/**
 * Normalization helper
 */
export function normalizeWalletOps(rows: any[]): WalletRow[] {
  if (!Array.isArray(rows)) return [];
  return rows.map((w) => ({
    wallet_id: String(w.wallet_id ?? w.id ?? ""),
    token_count: w.token_count ?? w.assets_count ?? null,
    balance: w.balance_native ?? w.sol_balance ?? null,
    creator_pubkey: w.creator_pubkey ?? null,
    status: w.status ?? (w.active ? "Active" : "Unknown"),
  }));
}

function useColumns(): ColumnDef<WalletRow, any>[] {
  return React.useMemo<ColumnDef<WalletRow, any>[]>(() => {
    return [
      // wallet
      {
        id: "wallet",
        header: "Wallet",
        accessorKey: "wallet_id",
        cell: (ctx) => {
          const v = ctx.getValue() as string;
          return (
            <Link
              className="underline font-mono"
              href={`/wallets/${encodeURIComponent(v)}`}
            >
              {hashShort(v)}
            </Link>
          );
        },
        sortingFn: "alphanumeric",
      },

      colNumber<WalletRow>("token_count", "Tokens", 0),

      {
        id: "balance",
        header: "Balance",
        accessorKey: "balance",
        cell: (ctx) => {
          const v = ctx.getValue();
          return v == null ? "—" : `${fmtNum(v, 3)} SOL`;
        },
        sortingFn: "alphanumeric",
      },

      {
        id: "creator",
        header: "Creator",
        accessorKey: "creator_pubkey",
        cell: (ctx) => {
          const v = ctx.getValue() as string | null;
          if (!v) return "—";
          return (
            <Link
              className="underline font-mono"
              href={`/creators/${encodeURIComponent(v)}`}
            >
              {hashShort(v)}
            </Link>
          );
        },
      },

      {
        id: "status",
        header: "Status",
        accessorKey: "status",
        cell: (ctx) => {
          const v = String(ctx.getValue() ?? "—");
          const tone =
            v === "Active"
              ? "bg-emerald-900/40 text-emerald-300"
              : "bg-muted";
          return (
            <span className={`px-2 py-0.5 rounded text-xs ${tone}`}>
              {v}
            </span>
          );
        },
      },

      {
        id: "actions",
        header: "",
        cell: (ctx) => {
          const w = ctx.row.original.wallet_id;
          return (
            <Link
              className="text-xs underline"
              href={`/wallets/${encodeURIComponent(w)}/history`}
            >
              Sweep History
            </Link>
          );
        },
      },
    ];
  }, []);
}

/**
 * Wallet Ops grid component
 */
export function WalletOpsGrid({
  rows,
  pageSize = 20,
}: {
  rows: any[];
  pageSize?: number;
}) {
  const data = React.useMemo(() => normalizeWalletOps(rows), [rows]);
  const columns = useColumns();

  return (
    <DataGrid<WalletRow>
      data={data}
      columns={columns}
      pageSize={pageSize}
      initialSorting={[{ id: "token_count", desc: true }]}
      emptyLabel="No wallets found."
    />
  );
}

export default WalletOpsGrid;
