import { fetchJson } from "@/lib/http";
import { API_BASE } from "@/lib/constants";
import type { KpiResponse, CompareResponse, LadderResponse, Alert } from "@/lib/types";

// Optional: if you want a typed return for trades later, you can define:
// export type TradesResponse = { items: any[]; page: number; page_size: number; total: number };

export const api = {
  // Dashboard
  getKpis: (): Promise<KpiResponse> =>
    fetchJson<KpiResponse>(`${API_BASE}/v1/kpis`),

  // Trades & Analysis
  getTrades: (page = 1, pageSize = 50): Promise<any> =>
    fetchJson<any>(`${API_BASE}/v1/trades?page=${page}&page_size=${pageSize}`),

  getCompare: (tradeId: string): Promise<CompareResponse> =>
    fetchJson<CompareResponse>(`${API_BASE}/v1/trades/${tradeId}/compare`),

  getLadder: (pairId: string | number, win = 8): Promise<LadderResponse> =>
    fetchJson<LadderResponse>(`${API_BASE}/v1/trades/${pairId}/ladder?window_slots=${win}`),

  // Creators
  getCreatorsCatalog: (qs: URLSearchParams): Promise<any[]> =>
    fetchJson<any[]>(`${API_BASE}/v1/creators/catalog?${qs.toString()}`),

  getCreatorProfile: (k: string): Promise<any> =>
    fetchJson<any>(`${API_BASE}/v1/creators/${k}/profile`),

  getCreatorActivity: (k: string, qs: URLSearchParams): Promise<any[]> =>
    fetchJson<any[]>(`${API_BASE}/v1/creators/${k}/activity?${qs.toString()}`),

  // Wallet Ops
  getWalletOps: (): Promise<any[]> =>
    fetchJson<any[]>(`${API_BASE}/v1/wallets/ops`),

  setWalletState: (
    id: string,
    state: string,
    reason = "Manual"
  ): Promise<any> =>
    fetchJson<any>(`${API_BASE}/v1/wallets/${id}/state`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ state, reason }),
    }),

  // Alerts (accepts URLSearchParams for back-compat OR a typed arg)
  getAlerts: (
    arg?: URLSearchParams | Record<string, any>
  ): Promise<{ items: Alert[]; page: number; page_size: number; total: number }> => {
    const params =
      arg instanceof URLSearchParams
        ? arg
        : new URLSearchParams(Object.entries(arg ?? {}));

    return fetchJson(`${API_BASE}/v1/alerts?${params.toString()}`);
  },

  // Rules
  getRules: (): Promise<any[]> =>
    fetchJson<any[]>(`${API_BASE}/v1/rules`),
};
